<template>
  <span class="version" :class="cls"></span>
</template>

<script>
export default {
  props: {
    ver: {
      type: String,
      required: true
    }
  },

  computed: {
    cls() {
      let str = this.ver.split(' ')[0].toLowerCase()
      str = str.split('/').join(' ')
      str = str.replace('2d', 'v2d').replace('3d', 'v3d')
      return str
    }
  }
}
</script>

