  background-repeat: no-repeat;
  height: 14px;
  width: 17px;
  display: inline-block;
  margin-top: 2px;
  margin-left: 6px;
  -webkit-box-flex: 0;
  flex: 0 0 auto;
  margin-right: 3px
}

